"use client";

import { useEffect } from "react";
import { useRouter, usePathname } from "next/navigation"; // Brug usePathname for ruteinformation
import { useAuthStore } from "../store/authStore";

const ProtectedRoute = ({ children }: { children: React.ReactNode }) => {
  const router = useRouter();
  const pathname = usePathname(); // Hent den aktuelle rute
  const role = useAuthStore((state) => state.role);

  useEffect(() => {
    // Check hvis brugeren er en editor og forsøger at tilgå en beskyttet rute
    if (pathname?.startsWith("/admin/settings") && role === "editor") {
      router.replace("/403"); // Omdiriger til adgangsnægtet side
    }
  }, [role, pathname, router]);

  return <>{children}</>; // Render børnene kun hvis brugeren har adgang
};

export default ProtectedRoute;
